import React from 'react';
import { Panel, PanelHeader, Group, Div, Button } from '@vkontakte/vkui';

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader>Тут заголовок</PanelHeader>
        Тут тело
<Group title="Меню">
    <Div>
        <Button size="xl" level="secondary" onClick={props.go} data-to="info">
            Информация
            </Button>
            </Div>
            </Group>
    </Panel>
);

export default Home;